/* App.css */
.app {
  background-color: #f7f7f7;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

/* App.css */
.grid-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px; /* 수정: 10px로 변경 */
  max-width: 900px;
}

.box {
  width: 100%;
  height: 200px;
  padding: 20px;
  border-radius: 10px;
  border: 2px solid #fff;
  background-color: #fff;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.main-title {
  background-color: #ff5f5f;
  color: #fff;
  grid-column: 1 / span 3;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* App.css */
.title {
  font-family: "Arial", sans-serif;
  font-size: 48px;
  font-weight: bold;
  text-align: center;
  margin: 0;
  pointer-events: none; /* 추가: 마우스 이벤트 비활성화 */
}

.personal-info {
  background-color: #ff9f5f;
  color: #fff;
}

.sns {
  background-color: #ffcf5f;
  color: #fff;
}

.hobbies {
  background-color: #5fff7f;
  color: #fff;
}

.profile-pic {
  background-color: #5fcaff;
  color: #fff;
}

.future-goals {
  background-color: #5f9fff;
  color: #fff;
}

.life-story {
  background-color: #a55fff;
  color: #fff;
}

.favorite-songs {
  background-color: #ff5fbf;
  color: #fff;
}

.favorite-food {
  background-color: #ff5f8f;
  color: #fff;
}

.role-model {
  background-color: #5f5fff;
  color: #fff;
}

.box:hover {
  background-color: #d9d9d9;
}

.content {
  margin-top: 10px;
}

ul {
  list-style: none;
  padding-left: 0;
}

img {
  max-width: 100%;
  max-height: 100%;
}

h1 {
  font-family: "Arial", sans-serif;
  font-size: 32px;
  font-weight: bold;
  text-align: center;
  margin: 0;
}

h2 {
  font-family: "Arial", sans-serif;
  font-size: 24px;
  font-weight: bold;
  text-align: center;
  margin: 0;
}

/* App.css */
.app {
  background-image: radial-gradient(#051937, #00316e, #0051a1);
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

/* App.css */
html,
body {
  margin: 0;
  padding: 0;
}
